@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans&display=swap);
body {
  margin: 0;
  font-family: 'Kumbh Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(245, 245, 245)

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.sidebar {
    display: grid;
    grid-area: sidebar;
    background-color:   rgb(255, 255, 255);
    border-radius: 20px;
    margin-top: 1rem;
    justify-content: center;
    height: 365px
}
.sidebarlist {
    font-size: 1.2rem;
}

a {
    display: block;
    list-style-type: none;
    text-align: center;
    color: rgb(107, 107, 107);
    margin: 1.5rem;
    cursor: pointer;
    text-decoration: none;
}

.list {
    justify-content: center;
    margin-top: 55px;
}


.content {
    display: grid;
    grid-area: content;
    background-color: #ffffff;
    border-radius: 20px;
    margin-top: 1rem;
    margin-left: 1rem;
}


.FormsController_formControl__1hHKa textarea {
    border: solid red 4px;
}

.FormsController_formControl__1hHKa.FormsController_error__2p3T6 span {
    color: red
}

.FormsController_fromSummaryError__3lJuo {
    border: red, 1px, solid;
    padding: 5px;
    color:rosybrown
}
.App {
  display: grid;
  grid-template-columns:  16rem 65rem;
  grid-template-rows: 70px 41rem;
  grid-template-areas: 'header header' 'sidebar content';
  justify-content: center; 
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}



.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.header {
    display: grid;
    grid-area: header;
    font-size: medium;
    background-color: rgb(255, 255, 255);
    justify-content: center;
    border-radius: 45px;
    margin-top: .7rem;
}

.github {
    color: rgb(65, 65, 65);
    margin-top: 11px;
    margin-right: 77rem
}
.login-block {
    margin-left: 1120px;
    margin-top: -3px;
}
